.page {
  font-family: "Inter", sans-serif;
  scroll-behavior: smooth;
  color: $main-color;
  
  &__body {
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 120px;

    &-title {
      font-weight: 700;
      font-size: 32px;
      line-height: 32px;
      text-align: center;
      letter-spacing: -1.5px;
      color: $main-color;
      margin-bottom: 48px;
    
      @include onTablet {
        font-size: 40px;
        line-height: 100%;
        margin-bottom: 64px;
      }
    
      @include onDesktop {
        font-size: 48px;
        line-height: 48px;
      }
    }

  }
}
